import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import layout from '@/views/layout/index'  //引入index组件

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: layout,
      children:[
        //用户
        {
          path:'/users',
          name:'用户列表页',
          component:() => import('@/views/users/list')
        },
        {
          path:'/users/add',
          name:'用户添加页',
          component:() => import('@/views/users/add')
        },
        {
          path:'/users/update',
          name:'用户修改页',
          component:() => import('@/views/users/update')
        },
        //党员互动
        {
          path:'/interaction',
          name:'帖子列表',
          component:() => import('@/views/interaction/list')
        },
        //轮播图
        {
          path:'/carousel',
          name:'轮播图列表',
          component:() => import('@/views/carousel/list')
        },
        {
          path:'/carousel/update',
          name:'轮播图修改',
          component:() => import('@/views/carousel/update')
        },
        //民主评议
        {
          path:'/review/add',
          name:'发表评议',
          component:() => import('@/views/review/add')
        },
        {
          path:'/review',
          name:'评议列表',
          component:() => import('@/views/review/list')
        },
        {
          path:'/review/perSummary',
          name:'评议详情页',
          component:() => import('@/views/review/perSummary')
        },
        //心得总结
        {
          path:'/summary',
          name:'总结列表',
          component:() => import('@/views/summary/list')
        },
        //思想汇报
        {
          path:'/reports',
          name:'汇报列表',
          component:() => import('@/views/reports/list')
        },
        //新闻管理
        {
          path:'/news/add',
          name:'添加新闻',
          component:() => import('@/views/news/add')
        },
        {
          path:'/news',
          name:'新闻列表',
          component:() => import('@/views/news/list')
        },
        {
          path:'/news/update',
          name:'新闻修改',
          component:() => import('@/views/news/update')
        },
        //登录
        {
          path:'/login',
          name:'登录',
          component:() => import('@/views/login/list')
        },
        //测试专用
        {
          path:'/test',
          name:'测试',
          component:() => import('@/components/HelloWorld')
        }
      ]
    }
  ]
})



