import Vue from 'vue'
import allapi from './allapi'
import base from './config'
import axios from 'axios'

let xhr = {
  get(url,params){
    return new Promise((resolve,reject) => {
      base.get(allapi[url],{
        params,
        headers:{"authorization":localStorage.getItem("authorization"),"token":localStorage.getItem("token")}   //在axios请求中，在localStorage里拿签证
      }).then(res => {
        resolve(res.data)
      }).catch(err => {
        console.log(err)
        reject(err)
      })
    })
  },
  post(url,params){
    return new Promise((resolve,reject) => {
      base.post(allapi[url],params,
        {
          headers:{"authorization":localStorage.getItem("authorization"),"token":localStorage.getItem("token")}
      }).then(res => {
        resolve(res.data)
      }).catch((err) => {
        console.log(err)
        reject(err)
      })
    })
  },
  qiniuGet(){
    return new Promise((resolve,reject) => {
      axios.get('/api/upload').then(res => {
        resolve(res.data)
      }).catch(err => {
        reject(err)
      })
    })

  }
}

Vue.prototype.$axios = xhr;





